  bloquearClick = () => {
    this.flipBook.getPageFlip();
  };
  nextButtonClick = () => {
    this.flipBook.getPageFlip().flipNext();
  };

  prevButtonClick = () => {
    this.flipBook.getPageFlip().flipPrev();
  };

  onPage = (e) => {
    this.setState({
      page: e.data,
    });
  };
  
  cambiar = () =>{
  
    this.state.contro = false


  }

